::-webkit-scrollbar{width:8px;height:8px;background-color:#fff}::-webkit-scrollbar-thumb{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:rgba(0,0,0,0.1)}.login-container .el-input{display:inline-block;height:47px;width:100%}.login-container .el-input input{background:transparent;outline:none;border-radius:30px;line-height:30px;height:38px !important;-webkit-appearance:none}.login-container .el-form-item{border:1px solid rgba(255,255,255,0.1);border-radius:5px;color:#454545}.login-container{position:absolute;width:100%;height:100%;background:#2b4b6b}.login-container .subContainer{width:450px;height:300px;background-color:#fff;border-radius:3px;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)}.login-container .subContainer .avatar_box{height:100px;width:100px;border:1px solid #eee;border-radius:50%;padding:10px;box-shadow:0 0 10px #ddd;position:absolute;left:50%;top:-25px;transform:translate(-50%, -50%);background-color:#fff}.login-container .subContainer .avatar_box img{width:100%;height:100%;border-radius:50%;background-color:#eee}.login-container .subContainer .loginFormContainer{width:100%;float:left}.login-container .subContainer .loginFormContainer .btnContainer{width:100%;text-align:center}.login-container .subContainer .loginFormContainer .btnContainer .labelContainer{width:100%;position:absolute}.login-container .subContainer .loginFormContainer .btnContainer .labelContainer .label{display:inline-block;margin:0 0 10px;color:#ccc;font-size:13px;text-align:left}
